*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  margin-top: -1px;
  margin-left: -1px;
  overflow: hidden;
  position: absolute;
  clip: rect(0, 0, 0, 0);
}

.linear-effect {
  padding: 70px 0;
  margin-top: 50px;
  text-align: center;
  background-color: black;
  color: white;
}

.linear-effect:after {
  content: '';
  height: 1px;
  display: block;
  width: 50%;
  margin: 0 auto;
  margin-top: 5px;
  background: linear-gradient(to right, #000, #0f0f0f 40%, #555 50%, #0f0f0f 60%, #000);
}

.block {
  padding: 30px 0;
  text-align: center;
}

.block .btn {
  padding: 20px 40px;
  width: 300px;
  background-color: cyan;
  border-radius: 40px;
  border: none;
  font-size: 20px;
  position: relative;
}

.block .btn:after {
  content: '';
  display: block;
  background: transparent url(../img/svg/telegram.svg) no-repeat;
  width: 20px;
  height: 20px;
  position: absolute;
  top: 50%;
  left: 50%;
  margin-top: -10px;
  margin-left: -10px;
  left: 220px;
}

.block .btn2:after {
  left: 230px;
}